.cronometro{
    background-image: url(../images/danca.jpg);
    background-position: center;
    background-size: cover;
    
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    
}

.cronometro_black{
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.651);
    top: 0px;
    left: 0;
    padding: 20px;
}

.timer{
    width: 100%;
    justify-content: center;
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    font-size: 1.7em;
    color: var(--begeClaro);
    font-family: 'Ginger', serif;
    
}

.cronometro h1{
    font-size: 2.5em;
    color: var(--begeClaro);
    font-family: 'Ginger', sans-serif;
    font-weight: lighter;
    margin-top: 20px;
}

.box_timer{
    margin: 10px;
    padding: 10px;
    border-radius: 10px;
    border-bottom: 1px solid var(--begeClaro);
    border-right: 1px solid var(--begeClaro);


    backdrop-filter: blur(6px);
    background-color: rgba(0, 0, 0, 0.308);
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;

    width: 155px;
    height: 85px;
    
    
    
}